  background-color: transparent;
  border: none;
  position: absolute;
  right: 2.5rem;
  top: 2rem;
  z-index: 555;
}

.block-title {
  text-align: center;
}

/* variables -------------*/
/* custom variables --------*/
.card {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #fff;
  border: 1px solid rgba(211, 211, 211, 0.4);
  border-radius: 8px;
  padding: 1.5rem;
}

/* variables -------------*/
/* custom variables --------*/
.fogdog {
  width: 100%;
  height: 70px;
  background-color: #f06d06;
}

.badass {
  width: 100%;
  height: 100px;
  background-color: #bada55;
}

body {
  background: #a2defb14 url("/assets/images/background.png");
  background-size: cover;
  background-position: left;
}
@media (min-width: 40rem) {
  body {
    background-position: center;
  }
}

.main-header {
  margin-block: 1rem;
  font-size: 25px;
  display: flex;
  justify-content: space-between;
}
.main-header .brand {
  letter-spacing: 2px;
  font-size: 1.75rem;
  color: #002265;
  font-weight: 600;
}
.main-header .brand span {
  color: #14279b;
}
.main-header .btn-menu svg {
  right: 3rem;
}
.main-header .btn-menu svg:last-of-type {
  position: fixed;
  color: #fff;
}
.main-header .main-navbar {
  position: relative;
  display: flex;
  z-index: 111;
}
.main-header .main-navbar ul {
  padding: 0.5rem 1.5rem;
  backdrop-filter: blur(1rem);
  transition: all 0.545s ease-out;
}
@media (max-width: 960px) {
  .main-header .main-navbar ul {
    background: #365185;
    position: fixed;
    padding-top: min(24vh, 12rem);
    inset: 0 0 0 70%;
    flex-direction: column;
  }
}
.main-header .main-navbar a {
  font-size: 1.1rem;
  letter-spacing: 1px;
  color: #002265;
}
@media (max-width: 960px) {
  .main-header .main-navbar a {
    color: #fff;
  }
}

.banner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2rem;
}
.banner .img-wrapper {
  flex: 0 1 max(40%, 350px);
  margin-inline: auto;
}
@media (min-width: 50rem) {
  .banner .img-wrapper {
    order: 1;
  }
}
.banner .banner__text {
  font-size: 0.8rem;
}
@media (min-width: 50rem) {
  .banner .banner__text {
    flex: 1 1 400px;
  }
}

.passions {
  padding-block: 3rem;
}
.passions .grid {
  justify-items: center;
  margin-top: 3rem;
  gap: 1.7rem;
  font-size: calc(0.8rem + 7%);
}
@media (max-width: 576px) {
  .passions .grid {
    font-size: 0.8rem;
  }
}
.passions .card {
  max-width: 370px;
}

.portfolio {
  padding-block: 3rem;
}
.portfolio .grid {
  justify-items: center;
  margin-top: 3rem;
  gap: 1.7rem;
  font-size: calc(0.8rem + 7%);
}
@media (max-width: 576px) {
  .portfolio .grid {
    font-size: 0.8rem;
  }
}
.portfolio .card {
  max-width: 400px;
  padding: 0.5rem;
  position: relative;
}
.portfolio .card:first-of-type h3 {
  color: #6c63ff;
}
.portfolio .card:nth-of-type(2) h3 {
  color: #4581f6;
}
.portfolio .card .card__content {
  position: absolute;
  top: 5%;
  left: 5%;
}
.portfolio .card .card-title {
  display: flex;
  position: relative;
  font-size: 1.6rem;
}
@media (min-width: 576px) and (max-width: 720px) {
  .portfolio .card .card-title {
    font-size: 1.4rem;
  }
}
.portfolio .card .card-title span:last-of-type {
  position: absolute;
  right: 0;
  top: 80%;
  font-size: 0.7rem;
}
.portfolio .img-wrapper {
  margin-top: 0.5rem;
  flex: 0 0 95%;
}

.contact-me .grid {
  justify-items: center;
}
.contact-me .card {
  width: 170px;
  background-color: transparent;
  box-shadow: none;
  border: none;
}

.main-footer {
  padding-top: 4rem;
  display: grid;
  align-items: center;
  justify-items: center;
  min-height: 150px;
  background-image: url("/assets/images/footer-bg.png");
  background-size: 2000px 650px;
  background-position: bottom;
  color: #fff;
  font-size: x-large;
}

