@import './base/mixins';
body {
  background: #a2defb14 url('/assets/images/background.png');
  background-size: cover;
  background-position: left;
  @media (min-width: 40rem) {
    background-position: center;
  }
}
.main-header {
  margin-block: 1rem;
  font-size: 25px;
  display: flex;
  justify-content: space-between;

  .brand {
    letter-spacing: 2px;
    font-size: $fs-4;
    color: $clr-title;
    font-weight: 600;

    span {
      color: $clr-title-light;
    }
  }
  .btn-menu svg {
    right: 3rem;
    &:last-of-type {
      position: fixed;
      color: #fff;
    }
  }
  .main-navbar {
    position: relative;
    display: flex;

    z-index: 111;
    //background-color: rgba(255, 255, 255, 0.507);
    ul {
      padding: 0.5rem 1.5rem;
      //background: hsl(0 0% 100% / 0.1);
      backdrop-filter: blur(1rem);
      transition: all 0.545s ease-out;
      @media (max-width: $lg) {
        background: #365185;
        position: fixed;
        padding-top: min(24vh, 12rem);
        inset: 0 0 0 70%;
        flex-direction: column;
      }
    }
    a {
      font-size: 1.1rem;
      letter-spacing: 1px;
      color: $clr-title;
      @media (max-width: $lg) {
        color: #fff;
      }
    }
  }
}

.banner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  gap: calc(2 * $gap);
  .img-wrapper {
    flex: 0 1 max(40%, 350px);
    margin-inline: auto;
    @media (min-width: 50rem) {
      order: 1;
    }
  }

  .banner__text {
    @media (min-width: 50rem) {
      flex: 1 1 400px;
    }

    font-size: $fs-0;
  }
}

.passions {
  padding-block: calc(2 * $spacer);

  .grid {
    justify-items: center;
    margin-top: calc(2 * $spacer);
    gap: calc(1.7 * $gap);
    font-size: calc($fs-0 + 7%);
    @media (max-width: $sm) {
      font-size: 0.8rem;
    }
  }
  .card {
    max-width: 370px;
  }
}

.portfolio {
  padding-block: calc(2 * $spacer);

  .grid {
    justify-items: center;
    margin-top: calc(2 * $spacer);
    gap: calc(1.7 * $gap);
    font-size: calc($fs-0 + 7%);
    @media (max-width: $sm) {
      font-size: 0.8rem;
    }
  }

  .card {
    max-width: 400px;
    &:first-of-type {
      h3 {
        color: #6c63ff;
      }
    }
    &:nth-of-type(2) {
      h3 {
        color: #4581f6;
      }
    }

    padding: 0.5rem;
    position: relative;
    .card__content {
      position: absolute;
      top: 5%;
      left: 5%;
    }

    .card-title {
      display: flex;
      position: relative;
      font-size: 1.6rem;
      @media (min-width: $sm) and (max-width: $md) {
        font-size: 1.4rem;
      }
      span {
        &:last-of-type {
          position: absolute;
          right: 0;
          top: 80%;
          font-size: 0.7rem;
        }
      }
    }
  }

  .img-wrapper {
    margin-top: 0.5rem;
    flex: 0 0 95%;
  }
}

.contact-me {
  .grid {
    justify-items: center;
  }
  .card {
    width: 170px;
    background-color: transparent;
    box-shadow: none;
    border: none;
  }
}

.main-footer {
  padding-top: 4rem;
  display: grid;
  align-items: center;
  justify-items: center;
  min-height: 150px;
  background-image: url('/assets/images/footer-bg.png');
  background-size: 2000px 650px;
  background-position: bottom;
  color: #fff;
  font-size: x-large;
}
.w-300 {
  color: $clr-title-light;
}
.hidden {
  display: none;
}
.hidden-lg {
  @media (min-width: $lg) {
    display: none;
  }
}

.flex-lg {
  @media (min-width: $lg) {
    display: flex;
  }
}
.collapsed-lg {
  .navigation {
    @media (max-width: $lg) {
      transform: translateX(100%);
    }
  }
}
