/* global -------------------*/
@import './variables';
body {
  font-family: $ff-poppins;
  color: $text-primary;
  font-size: 16px;
  max-width: 100vw;
}
/* end Global --------------*/
// end Global ------------

// typography ----------------
h1,
h2,
h3 {
  color: $clr-title;
  margin-block: 0.5rem;
}

h1 {
  font-size: $fs-4;
}
h2 {
  font-size: $fs-3;
}
h1 + p,
h2 + p,
h3 + p {
  margin-top: 0.7rem;
}
