@use './_variables';
@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin horizontal-list {
  @include reset-list;
  li {
    display: inline-block;
    margin: {
      left: -2px;
      right: 2em;
    }
  }
}

// media ------------------
@mixin mq($key) {
  $size: map-get(variables.$breakpoints, $key);
  @media only screen and (min-width: $size) {
    @content;
  }
}

// screen ------
@mixin screen() {
  $keys: map-keys(variables.$breakpoints);
  @each $screen in $keys {
    @media (min-width: map-get(variables.$breakpoints,$screen)) {
      &-#{$screen} {
        @content;
      }
    }
  }
}
@mixin grid {
  @for $nb from 2 to 6 {
    .grid-#{$nb} {
      @include screen {
        grid-template-columns: repeat($nb, 1fr);
      }
    }
  }
}

@mixin gap() {
  @for $nb from 1 to 6 {
    .gap-#{$nb} {
      gap: calc(($nb / 4) * $gap);
    }
  }
}
@mixin pt() {
  @for $nb from 1 to 6 {
    .pt-#{$nb} {
      @include screen {
        padding-top: calc(($nb / 4) * $spacer);
      }
    }
  }
}
// add triple dots ... after the variable name like this.
$style1: 100%, 70px, #f06d06;
$style2: (
  background: #bada55,
  width: 100%,
  height: 100px,
);

@mixin box($width, $height, $background) {
  width: $width;
  height: $height;
  background-color: $background;
}

.fogdog {
  @include box($style1...);
}

.badass {
  @include box($style2...);
}
