/* Utilities --------------*/
@use './variables';
.bg-elipse {
  background: rgba(14, 133, 243, 0.23);
  filter: blur(54px);
}

.container {
  max-width: min(1000px, 85%);
  margin-inline: auto;
  @media (min-width: 50em) {
    // color: red;
    // background-color: green;
  }
}

.collapsed {
  display: none;
}

.w-300 {
  font-weight: 300;
}
.w-600 {
  font-weight: 600;
}
.w-700 {
  font-weight: 700;
}

.box-shadow {
  box-shadow: 0px 5px 26px 5px rgba(0, 0, 0, 0.17);
}
.img-wrapper {
  margin-inline: auto;
}
.img-fluid {
  width: 100%;
  height: auto;
}
// grid -----------------------------
.grid {
  display: grid;
}

@include grid;
@include gap;

// flex -----------------------------
.flex {
  display: flex;
}
.fx-column {
  flex-direction: column;
}

//------------------------------

@include pt;
.card-dev {
  aspect-ratio: 12/9;
  background-color: rgba(219, 230, 230, 0.281) !important;
}
